@import "./components/components.fonts";
@import "./components/components.mixins";
@import "./components/components.general";
@import "./components/components.header";
@import "./components/components.footer";

* {
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-collapse: collapse;
}

img {
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none !important;
  -webkit-user-select: none !important;
}

a {
  color: inherit;
  cursor: pointer;
}
a:focus {
  outline: none;
}
button {
  cursor: pointer;
  border: none;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
}

$yellow: #ffc700;
$red: #c83007;
$black: #282828;
$white: white;

body {
  font-family: $font;
  color: $black;
  font-size: 18px;
  line-height: 130%;
  font-weight: 400;
}

.overflow {
  position: relative;
  overflow: hidden;
}
input,
select,
textarea,
a,
button {
  font-family: $font;
}

//НАЧАЛО - СЕКЦИЯ offer *****************************************************

.offer {
  padding: 50px 35px 60px 35px;
  background: url(../img/1_bg.png) 0% 0% no-repeat;
  background-size: cover;
  color: $white;
  overflow: hidden;
  position: relative;
}
.offer__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  position: relative;
}
.logo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 320px;
  height: 110px;
  margin-bottom: 60px;
}
.logo img {
  width: 250px;
  margin-bottom: 10px;
}
.wrapper-color {
  position: relative;
}
.color__abs-top {
  position: absolute;
  left: 0;
  top: 0;
  transform: skewX(-15deg);
  z-index: 2;
  background: $yellow;
  width: 95%;
  height: 90%;
}
.color__abs-bottom {
  position: absolute;
  left: 3%;
  top: 10%;
  transform: skewX(-15deg);
  z-index: 1;
  background: $red;
  width: 95%;
  height: 90%;
}
.text-skew {
  position: relative;
  z-index: 5;
  width: 100%;
  text-align: center;
}
.logo .color__abs-top {
  background: $white;
}
.main-title {
  font-weight: bold;
  font-size: 48px;
  line-height: 75px;
  margin-bottom: 50px;
  background: url(../img/title_bg.png) 190% 0% no-repeat;
  background-size: 110%;
  max-width: 820px;
  position: relative;
  z-index: 10;
}
.price {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  position: relative;
  z-index: 10;
}
.old-price {
  font-weight: bold;
  font-size: 24px;
  line-height: 135%;
  position: relative;
  margin-right: 45px;
  max-width: 500px;
}
.old-price:before {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: $white;
  position: absolute;
  left: 0;
  top: 50%;
}
.new-price {
  font-weight: bold;
  font-size: 48px;
  line-height: 150%;
  color: $yellow;
}
.button-pesan {
  width: 310px;
  height: 65px;
  color: $yellow;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  letter-spacing: 0.1em;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}
.button-pesan {
  .color__abs-top {
    background: $red;
  }
  .color__abs-bottom {
    background: $yellow;
  }
  .text-skew {
    margin-bottom: 10px;
  }
}
.btn-order {
  transition: all ease-out 0.3s;
}
.btn-order:hover {
  transform: scale(1.02);
}
.offer__abs-img {
  position: absolute;
  left: 50%;
  top: -50px;
  bottom: -60px;
}
.sale-block {
  width: 175px;
  height: 155px;
  font-weight: bold;
  font-size: 48px;
  line-height: 150%;
  color: $red;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 10;
  left: 95%;
  top: 10%;
  .text-skew {
    margin-bottom: 10px;
  }
}
.offer__abs1 {
  position: absolute;
  left: 300px;
  top: -135px;
  transform: rotate(90deg);
}
.offer__abs2 {
  position: absolute;
  top: 20%;
  right: 0;
}
.duration-sale {
  position: relative;
  z-index: 10;
  text-align: center;
}

//НАЧАЛО - СЕКЦИЯ advantages *****************************************************
.advantages {
  padding: 70px 35px;
}
.items__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
}
.item {
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.item-icon {
  width: 80px;
  height: 90px;
  transform: skewX(-15deg);
  background: $yellow;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  margin-left: 13px;
}
.item-icon img {
  transform: skewX(15deg);
}
.item-title {
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 20px;
  width: 100%;
}
.item-text {
  line-height: 165%;
  width: 100%;
}

//НАЧАЛО - СЕКЦИЯ about *****************************************************

.about {
  padding: 65px 35px;
  position: relative;
}
.about__container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}
.about__img {
  position: absolute;
  left: -70px;
  top: 0;
}
.about__wrapper {
  position: relative;
  z-index: 50;
  width: 56%;
}
.about__item {
  margin-bottom: 30px;
}
.about__title {
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $yellow;
  margin-bottom: 20px;
}
.about__title span {
  font-size: 30px;
  line-height: 37px;
  margin-right: 30px;
}
.about__text {
  font-size: 18px;
  line-height: 30px;
  color: $white;
  max-width: 550px;
}
.about__item1 {
  margin-left: 60px;
}
.about__item2 {
  margin-left: 30px;
}
.about__abs-bg {
  position: absolute;
  left: 380px;
  top: -65px;
}
.about__btn {
  position: absolute;
  left: 0;
  bottom: -175px;
  z-index: 999;
}
.about__abs1 {
  position: absolute;
  left: 0;
  top: -90px;
}
.about__abs2 {
  position: absolute;
  right: -120px;
  bottom: -161px;
  transform: rotate(90deg);
}

//НАЧАЛО - СЕКЦИЯ info *****************************************************

.info {
  padding: 70px 35px 0px 35px;
}
.info__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.info__item {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}
.info__item_img {
  width: 40%;
}
.info__item-text {
  width: 55%;
  color: #282828;
  line-height: 165%;
}
.info__item_title {
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 20px;
}
.info__item1 {
  flex-direction: row-reverse;
}

//НАЧАЛО - СЕКЦИЯ rewiev *****************************************************

.rewiev {
  padding: 20px 35px 60px 35px;
  position: relative;
}
.rewiev__title {
  font-weight: bold;
  font-size: 30px;
  line-height: 37px;
  color: #282828;
  margin-bottom: 40px;
}
.rewiev .item-icon {
  width: 80px;
  height: 90px;
  transform: skewX(0deg);
  background: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  margin-left: 13px;
}
.rewiev .item-icon img {
  transform: skewX(0deg);
}
.rewiev__abs {
  position: absolute;
  top: -200px;
  right: 0;
}
.rewiev__slider {
  display: none;
}

//НАЧАЛО - СЕКЦИЯ end *****************************************************

.end {
  padding: 70px 35px;
  background: url(../img/6_bg.png) 0% 0% no-repeat;
  background-size: cover;
  color: #ffffff;
  position: relative;
}
.end__container1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 70px;
}
.end__title {
  font-weight: bold;
  font-size: 30px;
  line-height: 37px;
  margin-bottom: 50px;
}
.end .item {
  width: 24%;
}
.end .item-title {
  color: $yellow;
  max-width: 260px;
}
.end__container2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}
.btn-buy {
  background: none;
  overflow: visible;
}
.end__big-title {
  font-weight: bold;
  font-size: 48px;
  line-height: 75px;
  margin-bottom: 30px;
  width: 100%;
}
.end__wrapper-text {
  width: 55%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.end__form {
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  margin-top: 20px;
}
.order__input {
  width: 358px;
  height: 60px;
  background: #ffffff;
  padding: 20px 30px;
  font-size: 18px;
  line-height: 20px;
  color: #c83007;
  border: none;
  border-radius: 0;
  margin-bottom: 30px;
  transform: skewX(-10deg);
  font-style: normal;
}
input:focus {
  border: 1px solid #c83007;
}
textarea:focus {
  border: 1px solid #c83007;
}
input::-moz-placeholder {
  color: #838383;
}
input::-webkit-input-placeholder {
  color: #838383;
}
.end__abs1 {
  position: absolute;
  left: 0;
  top: 35%;
}
.end__abs2 {
  position: absolute;
  top: -123px;
  right: 110px;
  transform: rotate(90deg);
}

